.no-webp .imagen-contacto {
background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
    background-image: url("../img/encuentra.webp");
    }

.imagen-contacto {
    background-position: center center;
    background-size: cover;
    
    display: flex;
    flex-direction: column;
    align-items: center;

    h2 {
        font-size: 4rem;
        font-weight: 900;
        color: #FFFFFF;
    }

    p {
        font-size: 1.8rem;
        color: #FFFFFF;
    }
}